@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap);
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Oswald', sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #f4f1de;
}



* {
  box-sizing: border-box;
}

td.question-board-cell {
    cursor: pointer;
    width: 100px;
    height: 200px;
    text-align: center;
    vertical-align: middle;
    background-color: #e07a5f;
    border-radius: 2px;
    color: white;
    border: 3px solid white;

    transition: 0.5s transform;
}

td.question-board-cell:hover {
    transform: scale(97%);
}

td.question-board-cell.is-revealed {
    background-color: #3d405b;
}
.question-board-title {
    text-align: center;
    font-size: 3rem;
    margin: 0 auto;
    color: white;
    background-color: #3d405b;
    max-width: 1200px;
    border: 3px solid white;
}

table {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;

    font-size: 2rem;

    border-collapse: collapse;
}

table th {
    z-index: 10;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    color: white;
    background-color: #3d405b;
}

.question-card {
    background-color: transparent;
    perspective: 1000px;
    width: 1200px;
    height: 500px;

    margin: 0 auto;
    font-size: 3rem;
}

.question-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    border: 3px solid white;

}

.question-card.flipped .question-card-inner {
    transform: rotateX(180deg);
}

.question-card-front, .question-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    color: white;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 1em;
}

.question-card-front h6, .question-card-back h6 {
    margin: 0;
}

.question-card-front {
    background-color: #e07a5f;
}

.question-card-back {
    background-color: #3d405b;
    transform: rotateX(180deg);
}

.question-card-content {
    flex: 1 1;
    margin: 0;
    font-size: 4rem;
    text-align: center;
}
